
@font-face {
    font-family: 'Cantarell Regular';
    src: local('Cantarell Regular'),
         url('../fonts/cantarell-regular.woff2') format('woff2'),
         url('../fonts/cantarell-regular.woff') format('woff'),
         url('../fonts/cantarell-regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Trend Sans One';
    src: local('Trend Sans One'),
         url('../fonts/trend-sans-one.woff2') format('woff2'),
         url('../fonts/trend-sans-one.woff') format('woff'),
         url('../fonts/trend-sans-one.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* === RESRT STYLE ALL BROWSER === */

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, img, ol, ul, li,
fieldset, form, label, legend, article, aside, figure, figcaption,
footer, header, menu, nav, section, time {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing:border-box;
}

ol, ul {
  list-style: none;
}

a {
	text-decoration: none;
}

/* === All PAGE STYLE === */

html{
	font-size: 10px;
	font-family: 'Arial Regular', 'Arial', sans-serif;
	color: #69747c;
	background: #FFF;
}

body {
    background-color: #f8fae8;
}

.wrapper {
    color: #64b33e;
    font-family: Cantarell Regular;
    width: 100%;
    min-width:480px;
    min-height: calc(100vh);   /* sticky footer*/
    display: flex;
}

/* === GAME START STATE === */

.wrapper.state__start {
    background: url(../img/bg-start-3.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.registration {
    overflow: hidden;
    width: 30%;
    background-color: #262208c2;
}

.form-registration {
	width: 80%;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}


.form-registration input[type="text"],
.form-registration input[type="submit"]{
    padding: 14px;
    margin: 10px 0;
    padding: 14px;
    margin: 10px 0;
    background-color: #fbf9e3;
    border-radius: 5px;
    border-style: inset;
}

.form-registration input[type="text"] {
    margin-top: 40px;
}

.form-registration p {
    width: 100%;
	display: flex;
    flex-direction: column;
    margin: 40px 0;
    font-size: 1.8rem;
    font-family: Cantarell Regular;
}

.form-registration input[type="submit"] {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    background-color: #529132;
    border-style: outset;
    color:#fbf9e3;
    border-color: #fbf9e3;
    font-size: 2.0rem;
    letter-spacing: 2.4;
    cursor: pointer;
}

.form-registration input[type="submit"]:hover {
    background-color: #4a7228;
}

/* === GAME DUEI STATE === */

.header__state__duel {
    width: 100%;
}

.state__duel__pattern {
    width: 100%;
    height: 36px;
    background: url(../img/castle-inside-pattern-1.jpg);
    border-bottom: 2px solid #fcfcb0;
}

.duel__pattern {
    width: 100%;
    height: 44px;
    background: url(../img/castle-inside-pattern-2.jpg);
    border-top: 2px solid #fcfcb0;
}


.state__duel {
    display: flex;
    flex-direction: column;
}

.main__state__duel {
    background: url(../img/bg-castle-inside-1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 100%;
    width: 100%;
    min-height: calc(100vh - 80px);
}

.managment__game {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-around;
}

.managment__player,
.managment__enemy {
    width: 300px;
    height: 80px;
    margin-top: -20px;
    background: url(../img/castle-inside-pattern-2.jpg);
    border: 2px solid #fcfcb0;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.managment__hit {
    width: 80px;
    height: 80px;
    margin-top: -20px;
    border: 2px solid #fcfcb0;
    border-radius: 10px;
    background-color: #1b0500;
}

.player__health,
.enemy__health {
    width: 80%;
    height: 20px;
    border: 1px solid #fcfcb0;
    border-radius: 3px;
}

.player__health__progress,
.enemy__health__progress {
    width: 100%;
    height: 100%;
    background-color: #529132;
    text-align: center;
    line-height: 20px;
    color: #fcfcb0;
}

.player__name,
.enemy__name {
    font-size: 1.8rem;
    padding: 8px 10px 0;
    width: 100%;
    height: 30px;
    overflow: hidden;
    text-align: center;
}

/* == Modal dialog ==*/

dialog {
    padding: 0;
    border: 0;
    border-radius: 0.6rem;
    box-shadow: 0 0 2rem black;
    margin-top: 200px;
    width: 400px;
    font-size: 1.6rem;
    text-align: justify;
    background-color: #fcfcb0;
}
  /* native backdrop */
dialog::backdrop {
    background-color: rgba(0, 0, 0, 0.6);
}
  /* polyfill backdrop */
dialog + .backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
}

dialog[open] {
    animation: slide-up 0.9s ease-out;
}

  /* The following styles are for older browsers when using
     the polyfill. These arenâ€™t necessary for Chrome/Firefox. */
dialog {
    display: none;
    position: absolute;
    margin: 0 auto;
    max-width: 80vw;
}

dialog[open] {
    display: block;
}

.modal__header,
.modal__body,
.modal__footer {
    padding: 1.5rem;
}

.modal__header {
    margin: 0;
    padding-bottom: 0.6em;
    border-top-left-radius: 0.6rem;
    border-top-right-radius: 0.6rem;
    font-size: 1.8rem;
    text-align: center;
    border-bottom: 1px solid #1b0500;
}

.modal__body p{
    padding: 5px 0;
}

.modal__footer {
    border-bottom-left-radius: 0.6rem;
    border-bottom-right-radius: 0.6rem;
    background-color: #1b0500;
    justify-content: space-around;
    display: flex;
}

#hitHurricane {
    background: url(../img/hit-icon-1.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    background-color: #1b0500;
}

#hitSnowfall {
    background: url(../img/hit-icon-2.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    background-color: #1b0500;
}

#hitFire {
    background: url(../img/hit-icon-3.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    background-color: #1b0500;
}

#hitStarfall {
    background: url(../img/hit-icon-4.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    background-color: #1b0500;
}

#hitVortex {
    background: url(../img/hit-icon-5.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    background-color: #1b0500;
}

.hit__icon {
    width: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    height: 40px;
    background-position: 50%;
    border: 1px solid wheat;
    /* background-color: #fcfcb0; */
    box-shadow: 0 0 2rem #faf9f9;
    border-radius: 10px;
    cursor: pointer;
}

#modal__name__player,
#modal__name__enemy,
.modal__hit__text {
    color: #529132;
    font-size: 1.8rem;
}


.task__screen {
    position: absolute;
    padding: 0;
    border: 0;
    border-radius: 0.6rem;
    box-shadow: 0 0 2rem black;
    /* margin-top: 80px; */
    width: 80%;
    height: 70%;
    left:10%;
    top: 20%;

    font-size: 1.6rem;
    text-align: justify;
    background-color: #fcfcb0;
}

.task__screen[open] {
    animation: slide-up 0.9s ease-out;
}

.task__header,
.task__body,
.task__footer {
    padding: 1.5rem;
}

.task__header {
    margin: 0;
    padding: 30px;
    border-top-left-radius: .6rem;
    border-top-right-radius: .6rem;
    font-size: 2.2rem;
    text-align: center;
    background-color: #1b0500;
    color: #fcfcb0;
}

.task__header span {
    color: #529132;
    letter-spacing: 2.0;
}

.task__condition__wrapper {
    display: flex;
    justify-content: center;
}

.task__condition,
.task__answer {
    padding: 10px;
    /* border-bottom: 1px solid #1b0500; */
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3.0rem;
}

.task__answer input {
    text-align: center;
    font-size: 2.8rem;
    padding: 15px;
    background-color: #fcfcb0;
    border: none;
    border-bottom: 1px solid #1b0500;
}
.task__answer__button {
    width: 200px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    background-color: #529132;
    color: #fbf9e3;
    border-radius: 6px;
    font-size: 2rem;
    letter-spacing: 2.4;
    cursor: pointer;
    box-shadow: 0 0 1rem #000;
    margin: 0 auto;
}

.task__condition {
    color: #1b0500;
    font-size: 2.8rem;
    border-bottom: 1px solid #1b0500;
}

.task__answer__button:hover {
    background-color: #4a7228;
}


.modal__footer {
    border-bottom-left-radius: 0.6rem;
    border-bottom-right-radius: 0.6rem;
    background-color: #1b0500;
    justify-content: space-around;
    display: flex;
}

@keyframes slide-up {
    0% {
      opacity: 0;
      transform: translate(0, 20px);
    }

    100% {
      opacity: 1;
      transform: translate(0, 0);
    }
}

