.screen__score {
    position: absolute;
    z-index: 2;
    padding: 0;
    border: 0;
    border-radius: 0.6rem;
    box-shadow: 0 0 2rem black;
    width: 80%;
    height: 90%;
    left:10%;
    top: 5%;
    font-size: 2.2rem;
    text-align: justify;
    background-color: #fcfcb0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.screen__score h2 {
    padding: 10px;
    font-size: 3.0rem;
}

.screen__score table {
    width: 80%;
    border: 1px solid #1b0500;
    border-collapse: collapse;
}


.screen__score table caption {
	color: #1b0500;
    font-size: 2.6rem;
    padding: 10px;
}

.screen__score table th,
.screen__score table td {
    padding: 4px 20px;
    border: 1px solid #1b0500;
    font-size: 2.0rem;
    color: #1b0500;

}

.images__for__animation {
    display: none;
}

.remove {
    display: none;
}

.block__hit {
    margin-left: 20px;
}

.block__hit canvas{
    display: block;
    position: absolute;
    left: 20%;
    bottom: 20%;
    /* display: none; */
}

.answer__error {
    color: red;
    font-size: 3.0rem;
}

.answer__correct {
    color: #529132;
    font-size: 3.0rem;
}

